html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.w-30 {
  width: 30%;
}

.w-50 {
  width: 50%;
}

.w-70 {
  width: 70%;
}

.w-65 {
  width: 65%;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo {
  background-color: #bd0707;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-rojo:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #bd0707;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

/** UI **/
.header {
  background-color: #E08709;
  padding: 1rem 0 1rem 0;
}

.contenido-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  padding: 1rem 0;
  background-color: #E08709;
  margin-top: 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding: 1rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  margin-bottom: 0;
  margin-top: 0;
  color: #FFFFFF;
  text-align: center;
}

.formulario fieldset {
  margin-bottom: 1rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #333333;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 30rem;
}

.factura {
  background-color: #9cda9f;
  border: 1px solid #246327;
  border-radius: 10px;
}

.datos-cliente,
.datos-venta {
  width: 100%;
  max-width: 90%;
  margin: auto;
  margin-bottom: 2rem;
}

.form-venta {
  background-color: #e3ecef;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  margin: auto;
  border: 2px solid #78909c;
  padding: 1rem;
  border-radius: 10px;
  margin-top: 1rem;
}

.action_cliente {
  display: flex;
  align-items: center;
}
.action_cliente a {
  margin-top: 0;
}
.action_cliente a.ocultar {
  display: none;
}
.action_cliente h4 {
  margin: 1rem 1rem 1rem 0;
}

.borde-tabla {
  max-width: 90%;
  margin: auto;
  border: 2px solid #78909c;
  padding: 0;
  border-radius: 10px;
  margin-bottom: 2rem;
}

.tabla-venta {
  width: 100%;
  margin: auto;
  border-spacing: 0;
  border-radius: 10px;
}
.tabla-venta .tabla-titles {
  background-color: #0e5a80;
}
.tabla-venta tbody,
.tabla-venta thead {
  background-color: #e3ecef;
}
.tabla-venta tbody th,
.tabla-venta thead th {
  color: #FFFFFF;
  text-transform: uppercase;
}
.tabla-venta tbody a,
.tabla-venta thead a {
  margin: 1rem;
}
.tabla-venta tbody tr td .input-cod,
.tabla-venta thead tr td .input-cod {
  margin: auto;
  max-width: 40%;
  display: flex;
}
.tabla-venta tbody .text-tbl,
.tabla-venta thead .text-tbl {
  max-width: 100%;
}
.tabla-venta tfoot {
  background-color: #e3ecef;
}
.tabla-venta tfoot td {
  font-weight: bold;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #333333;
}

.navegacion {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav_list {
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
}

li {
  list-style: none;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}
li:hover {
  background-color: #E08709;
}

.nav_item {
  position: relative;
  padding: 2rem;
}

.nav_item_drop {
  padding-left: 0;
  display: none;
  position: absolute;
}

.nav_item_drop > li {
  width: 20rem;
  padding: 1rem;
  background-color: #E08709;
}

.nav_item_drop > li:hover {
  background-color: #71B100;
}

.nav_item:hover > .nav_item_drop {
  display: block;
  border-radius: 10px;
}

/** Paginas Internas **/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #bd0707;
}
.alerta.exito {
  background-color: #71B100;
}

table.tablas {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.tablas thead {
  background-color: #333333;
}
table.tablas thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.tablas tbody td {
  text-align: center;
}
table.tablas tbody .boton-tabla {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}